<script setup lang="ts">
import { computed } from "vue";
import Images from "../../Image/Images.vue";
import Albums from "../../Photos/Albums.vue";
import FancyboxCombine from "../../Fancybox/FancyboxCombine.vue";
import { isGridContainer, isLineContainer } from "@utils/website/block-site.utils";
import type { PagePhotoAlbumSection } from "types/models/website/section/photo-section";
import { ImageRenderSizes } from "@enums/image.enum";
import { LoadingStateTypes } from "@enums/components.enum";
import SkeletonLoader from "@components-lib/components/SkeletonLoader.vue";

const props = defineProps<{ section: PagePhotoAlbumSection; isLoading?: boolean }>();

const isGrid = computed(() => isGridContainer(props.section.style));
const isLine = computed(() => isLineContainer(props.section.style));
const images = computed(() => props.section.album.photos.map((photo) => photo.image));
</script>

<!--TODO add display album name if it's a line or a slideshow-->
<template>
  <template v-if="isLoading">
    <SkeletonLoader :type="LoadingStateTypes.GALLERY" />
  </template>
  <section v-else>
    <Albums v-if="isLine" :images="images" :album-name="section.album.name" class="radius-0" />
    <Images v-else-if="isGrid" :images="images" :size="ImageRenderSizes.SIZE876" />
    <FancyboxCombine
      v-else
      :auto-play="section.autoPlay"
      :show-thumbs="section.showThumbnails"
      :data="images.map((image) => ({ image }))"
    />
  </section>
</template>
